<!--定义商户行业-->
<template>
  <div>
    <el-card style="width: 100%">
      <div style="float: right;margin: 10px 0 30px 0">
        <el-button type="primary" @click="new_industry()">新增行业</el-button>
      </div>
      <el-table
        :data="tableData"
        @sort-change="sortColumn"
        class="account-plat-fixed-right account-plat-fixed "
        border
        style="width: 100%"
        ref="tableData"
        tooltip-effect="dark"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
        <el-table-column
          fixed
          align="center"
          type="index"
          label="序号"
          width="100"
        >
        </el-table-column>
        <el-table-column
          v-if="industry_id"
          prop="bii_id"
          label="行业名称"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="bii_name"
          label="行业名称"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          align="center"
        >
          <template slot-scope="scope">
            <!-- <el-button @click="industry_delete(scope.$index, scope.row)">删除</el-button> -->
            <el-button @click.native.stop="industry_delete(scope.$index, scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="margin-top: 30px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParam.currentPage"
        :page-sizes="[10, 20, 50, 100,200]"
        :page-size="queryParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryParam.totalRow">
      </el-pagination>
    </el-card>
    <el-dialog
      :show-close="false"
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      width="35%"
      >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '新增行业' }}</span>
      </div>
      <div class="pop-up">
        <el-form :model="industryForm" :rules="industryFormrules"  ref="industryForm">
          <el-row :gutter="24">
            <el-col :md="18">
              <el-form-item label="行业名称:" label-width="150px" prop="bii_name">
                <el-input v-model="industryForm.bii_name"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose()">取 消</el-button>
        <el-button type="primary" @click="submitForm('industryForm')">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "DefineMerchantIndustry",
  data(){
    let bii_name1 = (rule, value, callback) => {
      if (value.trim().length === 0) {
        callback(new Error('行业名称不能为空'));
      } else {
        callback();
      }
    };
    return{
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      industry_id:false,
      tableData:[],
      dialogVisible: false,//新增行业弹窗开关
      industryForm:{
        bii_name:''
      },
      industryFormrules:{
        bii_name: [
          {required: true, validator: bii_name1, trigger: 'blur'}
        ],
      }
    }
  },
  created() {
    console.log("新建活动userInfo:" + JSON.stringify(this.customGlobal.userInfoExt));
    this.queryParam.currentPage = 1;
    this.industry_select();
  },
  methods:{
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.industry_select();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.industry_select();
    },
    sortColumn(column) {
      console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        console.info("属性名称：" + column.prop + ",排序方式：" + column.order);

        this.sortOrderCol = "vci." + column.prop + " " + orderFlag;
        console.info("用户选择排序列：" + this.sortOrderCol);
      } else {
        this.sortOrderCol = '';
      }
    },
    new_industry(){
      this.industryForm.bii_name='';
      this.dialogVisible = true;
    },
    handleClose(){
      this.dialogVisible = false;
    },
    submitForm(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.industry_add();
          this.dialogVisible = false;
        } else {
          // console.log('error submit!!');
          return false;
        }
      });
    },
    industry_add() {
      let that = this;
      let _jsondata_ = {
        bii_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        bii_province_id: that.customGlobal.userInfoExt.cui_province_id,
        bii_city_id: that.customGlobal.userInfoExt.cui_city_id,
        bii_county_id: that.customGlobal.userInfoExt.cui_county_id,
        bii_station_id: that.customGlobal.userInfoExt.cui_station_id,
        bii_name: that.industryForm.bii_name,
        bii_cui_id:that.customGlobal.userInfoExt.cui_uuid,
      };

      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.industry_add,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        console.log("异步调用成功返回:" + JSON.stringify(res));
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">行业新增成功</span>'
          });
          this.queryParam.currentPage = 1;
          this.industry_select();
        } else {
          console.info("行业新增失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }

      }, error => {
        console.log("异步调用出错:" + JSON.stringify(error));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '行业新增失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '行业新增失败！', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    industry_select() {
      let that = this;
      let _jsondata_ = {
        bii_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        bii_province_id: that.customGlobal.userInfoExt.cui_province_id,
        bii_city_id: that.customGlobal.userInfoExt.cui_city_id,
        bii_county_id: that.customGlobal.userInfoExt.cui_county_id,
        bii_station_id: that.customGlobal.userInfoExt.cui_station_id,
        perPageSize: that.queryParam.pageSize,
        currentPage: that.queryParam.currentPage,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.Bank_industry_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        console.log("异步调用成功返回:" + JSON.stringify(res));
        if (res.data.status == 'success') {
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;
          loading.close();
          // this.$message({
          //   type: 'success',
          //   showClose: true,
          //   duration: 3000,
          //   dangerouslyUseHTMLString: true,
          //   message: '<span style="color:black;">行业查询成功</span>'
          // });
        } else {
          that.tableData = [];
          console.info("行业查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }

      }, error => {
        console.log("异步调用出错:" + JSON.stringify(error));
        loading.close();
        that.tableData = [];
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '行业查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        that.tableData = [];
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '行业查询失败！', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    industry_delete(index,row) {
      let _jsondata_ = {
        bii_id:row.bii_id,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.industry_delete,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        console.log("异步调用成功返回:" + JSON.stringify(res));
        if (res.data.status == 'success') {
          loading.close();
          this.$message({
            type: 'success',
            showClose: true,
            duration: 3000,
            dangerouslyUseHTMLString: true,
            message: '<span style="color:black;">行业删除成功</span>'
          });
          this.queryParam.currentPage = 1;
          this.industry_select();
        } else {
          console.info("行业删除失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }

      }, error => {
        console.log("异步调用出错:" + JSON.stringify(error));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '行业删除失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '行业删除失败！', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
  }
}
</script>

<style scoped>
.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}
</style>



